@import "../core/variables.scss";

.contact {
  &_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    font-family: $form_font;
    @media all and (max-width: 768px) {
      width: 85%;
    }
  }
  &_input {
    padding: 0.6rem;
    margin: 10px;
    border-radius: 5px;
    border: solid 1px #00ffa8;
    font-family: inherit;
  }
  &_label {
    margin-left: 10px;
    font-size: 1rem;
  }
  &_inputs_section {
    display: flex;
    flex-wrap: wrap;
  }
  &_textarea_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    .contact_input {
      @media all and (max-width: 768px) {
        width: 85%;
      }
    }
  }
  &_asterisk {
    font-weight: bold;
  }
  &_submit {
    font-size: 1rem;
    padding: 0.6rem 0.8rem;
    border-radius: 5px;
    border: solid 2px $form_secondary_color;
    background-color: $form_secondary_color;
    font-family: inherit;
    margin-top: 15px;
    @media all and (max-width: 768px) {
      margin-top: 10px;
    }
    &:hover {
      font-weight: bold;
      background-color: $form_primary_color;
    }
  }
}
